function SecessionRequestTable({
  services,
  useService,
  notifySuccess,
  notifyError,
}) {
  const { secession, user } = services;

  let service = useService({
    [SERVICE?.BULK_RETRIEVE]: user.find,
  });
  const { dispatchRequest } = service;

  useEffect(() => {
    dispatchRequest({
      type: [SERVICE?.BULK_RETRIEVE],
      payload: {
        "order[updatedAt]": "DESC",
        "order[createdAt]": "DESC",
        "options[paranoid]": false,
        sudo: true,
        fake: true,
        include: ["secession", "profile"],
      },
      toast: { success: notifySuccess, error: notifyError },
    });
  }, []);

  const Permit = ({ row }) => {
    const [permission, setPermission] = useState(row?.permission);
    let { dispatchRequest: _d } = useService({
      [SERVICE?.UPDATE]: user?.updateByID,
    });

    function handleChange(e, value) {
      // TODO: Make Permission change request
      _d({
        type: SERVICE?.UPDATE,
        payload: { id: row?.id, data: { permission: !permission } },
        reload: true,
        toast: { success: notifySuccess, error: notifyError },
      });
      setPermission(value);
    }
    return (
      <small className="d-flex" style={{ gap: 10, alignItems: "center" }}>
        Deny
        <Switch
          color="default"
          onChange={handleChange}
          name="permission"
          value={row?.permission}
          size="small"
        />
        Accept
      </small>
    );
  };

  return (
    <>
      <TableGenerator
        {...{ service }}
        mapping={{
          id: "User ID",
        }}
        omit="*"
        extras={[
          "name",
          "user_id",
          "phone_number",
          "level" /*  "permission" */,
        ]}
        transformers={{
          name: ({ row }) => {
            return (
              <div className="media d-flex align-items-center">
                <div className="avatar avatar-xl mr-4">
                  <div className="rounded-circle overflow-hidden img-fluid">
                    <IdenticonAvatar size={30} alt="" id={row.id} />
                  </div>
                </div>

                <div className="media-body">
                  <div className="mb-0 fs--1">
                    <Link to="/to_user_information">
                      {row?.profile?.pname || row?.profile?.lname || "Untitled"}
                    </Link>
                  </div>
                  <div
                    className="d-flex align-items-center"
                    style={{ gap: 16, fontSize: 12 }}
                  >
                    <Moment withTitle format="MMM Do, Y hh:m A" trim>
                      {row?.createdAt}
                    </Moment>
                    {/*  <span>
                      <i
                        className="fa fa-star"
                        style={{ color: "#089248" }}
                        aria-hidden="true"
                      ></i>{" "}
                      2.5
                    </span> */}
                  </div>
                </div>
              </div>
            );
          },
          user_id: ({ row }) => {
            return <>{row?.id}</>;
          },
          level: ({ row }) => {
            return row?.access_level;
          },
          permission: Permit,
          phone_number: ({ row }) => {
            return row?.profile?.phone ? (
              <a href={`tel:${row?.profile?.phone}`}>{row?.profile?.phone}</a>
            ) : (
              <span className="badge light badge-danger">
                <i className="fa fa-circle text-danger mr-1" />
                Not specified
              </span>
            );
          },
        }}
      />
    </>
  );
}

function ApprovedSecessionTable({ services, useService }) {
  const { secession } = services;

  let service = useService({
    [SERVICE?.BULK_RETRIEVE]: secession.find,
  });
  const { dispatchRequest } = service;

  useEffect(() => {
    dispatchRequest({
      type: [SERVICE?.BULK_RETRIEVE],
      payload: {
        "order[updatedAt]": "DESC",
        "order[createdAt]": "DESC",
        "options[paranoid]": false,
        "filter[status]": "accepted",
        sudo: true,
        fake: true,
      },
      toast: { success: notifySuccess, error: notifyError },
    });
  }, []);
  const action = (
    <div className="d-flex " style={{ gap: 20 }}>
      <a href="#" className="themify-glyph-306"></a>
      <a href="#" className="themify-glyph-165"></a>
    </div>
  );

  return (
    <>
      <TableGenerator
        {...{ service }}
        mapping={{
          id: "User ID",
        }}
        omit="*"
        extras={[
          "id",
          "description",
          "due_date",
          "approval_status",
          "approval_date",
        ]}
        transformers={{
          id: ({ row }) => row?.id,
          description: ({ row }) => row?.description,
          due_date: ({ row }) => (
            <Moment durationFromNow date={row?.due_date} trim></Moment>
          ),
          approval_status: ({ row }) => row?.status,
          approval_date: ({ row }) => (
            <Moment withTitle format="MMM Do, Y hh:m A" trim>
              {row?.approval_date}
            </Moment>
          ),
        }}
      />
    </>
  );
}
function PastSecessionListTable({ services, useService }) {
  const { secession } = services;

  let service = useService({
    [SERVICE?.BULK_RETRIEVE]: secession.find,
  });
  const { dispatchRequest } = service;

  useEffect(() => {
    dispatchRequest({
      type: [SERVICE?.BULK_RETRIEVE],
      payload: {
        "order[updatedAt]": "DESC",
        "order[createdAt]": "DESC",
        "options[paranoid]": false,
        "filter[status]": "accepted",
        sudo: true,
        fake: true,
      },
      toast: { success: notifySuccess, error: notifyError },
    });
  }, []);
  return (
    <>
      <TableGenerator
        {...{ service }}
        mapping={{
          id: "User ID",
        }}
        omit="*"
        extras={["user", "email", "last_invite_sent", "role"]}
        transformers={{
          user: ({ row }) => (
            <Link to="/to_user_information">
              <div className="media d-flex align-items-center">
                <div className="avatar avatar-xl mr-4">
                  <div className="">
                    <IdenticonAvatar size={30} alt="" id={row.id} />
                  </div>
                </div>
                <div className="media-body">
                  <h5 className="mb-0 fs--1">{row?.user?.pname}</h5>
                </div>
              </div>
            </Link>
          ),
          email: ({ row }) => row?.user?.email,
          last_invite_sent: ({ row }) => row?.user?.email,
          role: ({ row }) => (row?.user?.isAdmin ? "Administrator" : "Regular"),
        }}
      />
    </>
  );
}

function notifySuccess() {
  toast.success("Success !", {
    position: "top-right",
    autoClose: 3000,
    hideProgressBar: true,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
  });
}

function notifyError(error) {
  toast.error(error || "Request Error!", {
    position: "top-right",
    autoClose: 5000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
  });
}
